.cart {
	.res {
		display: none; } }
@media (max-width:1024px) {
	.cart {
		.row.just ,.row.firstRow {
			.col.about {
				width: 28%;
				.infoProduct {
					.nameProduct {
						font-size: 14px; }
					.aticule , .color {
						font-size: 12px; } } }
			.col.size {
				width: 15%;
				.customSelect {
					width: 80%; } }
			.col.height {
				width: 15%;
				.customSelect {
					width: 80%; } }
			.col.count {
				width: 15%; }
			.col.price {
				width: 15%; }
			.col.close {
				width: 3%; } } }
	.orderSector {
		.form {
			.longField {
				width: 32%;
				margin: 10px .5%; }
			.smallField {
				width: 10%;
				margin: 10px .5%; }
			.customSelect {
				width: 100%; }
			.veryLongField {
				width: 99%;
				margin: 10px .5%; } } }
	.orderSector {
		margin-bottom: 20px;
		float: left; }
	.recomendation {
		.card {
			width: 28vw;
			margin: 0 2vw;
			max-width: 400px;
			img {
				width: 100%; } }
		.owl-nav {
			display: block;
			.owl-prev {
				margin-left: 10px; }
			.owl-next {
				margin-right: 10px; } } } }
@media (max-width:845px) {
	.cart {
		.result {
			.infoOrder {
				margin-top: 20px; } } } }
